import Styled from "styled-components";

export const InputStyled = Styled.input`
  border: 2px solid #ccc;
  border-radius: 5px;
  outline: none;
  padding: 8px;
  margin: 2px;
  font-size: 0.95rem;
`;

export const Paragraph = Styled.p`
  margin: 2px;
  padding: 4px;
  font-size: 1.02rem;
`;
